.navContainer {
  @include flex(row, space-between, center);
  margin: 30px 0;
  max-width: 1600px;
  width: 100%;
  padding: 0 40px;
}
.navigation {
  @include flex(row, end, end);
}
.authorization {
  @include flex(row, end, end);
}
.button {
  display: inline-block;
  margin: 10px;
  padding: 8px 16px;
  background-color: transparent;
  color: $white;
  border: 2px solid $white;
  box-shadow: 0 0 4px rgba(255, 255, 255, 1);
  border-radius: 30px;
  text-decoration: none;
  text-align: center;
  font-size: 22px;
  cursor: pointer;
}
.button:hover {
  scale: 1.1;
}
